<template>
  <div class="index">
	<div>
      <!-- 二级路由 -->
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
    <div>
		<van-tabbar v-model="tabbarActive" class="tab-bar">
		  <van-tabbar-item :name="item.name" :icon="item.icon" v-for="(item, index) in navigaDatar" :key="index" @click="togglePage({name: item.name})">{{item.title}}</van-tabbar-item>
		</van-tabbar>
	</div>
  </div>
</template>

<script>
 	import {createNamespacedHelpers} from 'vuex'

	const {mapState} = createNamespacedHelpers('indexModule');
	  
	export default {
	  	name: "index",
		
		data() {
		  return {
		    tabbarActive: 'home'
		  }
		},
		//计算属性
		computed: {
      		...mapState(['navigaDatar'])
		},

		methods: {
			togglePage(o) {
				this.$router.push(o);
			}
		},

		created() {
			// console.log('this.$route ==> ', this.$route);
			//纠正底部导航的标签
      		this.tabbarActive = this.$route.name;
		}
  	}
	
</script>
